export const levelToJapanese = (level) => {
  switch (level) {
    case 1:
      return ''
    case 2:
      return ''
    case 3:
      return ''
    case 4:
      return ''
    case 5:
      return ''
    default:
      return ''
  }
}

export const resultsToJapanese = (correct, quizCount) => {
  switch (Math.round(correct*10/quizCount)) {
    case 1:
      return ''
    case 2:
    case 3:
    case 4:
      return ''
    case 5:
    case 6:
      return ''
    case 7:
    case 8:
    case 9:
      return ''
    case 10:
      return ''
    default:
      return ''
  }
}



